<template>
  <div class="subPage">
    <div class="pageTitle">异常信息统计</div>
    <div>
      <el-date-picker v-model="dates" type="daterange" align="right" unlink-panels range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions"  format="yyyy 年 MM 月 dd 日"  value-format="yyyy-MM-dd">
      </el-date-picker>
      <el-button class="btn_search" type="success" round @click="search">搜索</el-button>
    </div>
    <div class="contents">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="dep" label="科室" width="180"></el-table-column>
        <el-table-column prop="ycbz" label="医疗类型" width="180" ></el-table-column>
        <el-table-column prop="typeName" label="废物类型"  width="180"></el-table-column>
        <el-table-column prop="cjweight" label="采集重量"  width="180"></el-table-column>
        <el-table-column prop="count" label="异常状态" width="180"></el-table-column>
        <el-table-column prop="ycclsj" label="异常时间" width="180"></el-table-column>
        <el-table-column prop="ycclr" label="异常处理人" width="100"></el-table-column>
        <el-table-column prop="ycyy" label="异常原因" width="180"></el-table-column>
        <el-table-column prop="cjcode" label="采集条码" width="180"></el-table-column>
        <el-table-column prop="cjsj" label="采集时间" width="180"></el-table-column>
        <el-table-column prop="cjweight" label="采集重量" width="100"></el-table-column>
        <el-table-column prop="cjperson" label="采集人员" width="100"></el-table-column>
        <el-table-column prop="jjperson" label="科室交接人" width="100"></el-table-column>
        <el-table-column prop="hjsj" label="核交时间" width="180"></el-table-column>
        <el-table-column prop="hjperson" label="核交人员" width="180"></el-table-column>
        <el-table-column prop="hjweight" label="核交重量" width="100"></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import {_Widget} from "../../common";
import {Downloader} from "../../common";
import {commonConfig} from "../../common";

export default {
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      dates: '',
      tableData:[]
    };
  },
  methods: {
    search(){
      var thisObject = this;
      var data = {
        type:'ycxxtj',
        startdate:thisObject.dates[0],
        enddate:thisObject.dates[1],
      };
      var url = commonConfig.baseURL + "of_yf_cx";
      Downloader.loadXMLAsync(url + "?as_json=" + JSON.stringify(data), function(e) {
        var xmldoc = e.response;
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          for(var i = 0; i < res.data.length; i++){
            switch (res.data[i].type){
              case 1:
                res.data[i].typeName = "感染性";
                break;
              case 2:
                res.data[i].typeName = "损伤性";
                break;
              case 3:
                res.data[i].typeName = "病理性";
                break;
              case 4:
                res.data[i].typeName = "化学性";
                break;
              case 5:
                res.data[i].typeName = "药物性";
                break;
              default:
            }
          }
          thisObject.tableData = res.data;
        }else{
          thisObject.$message({
            type: 'error',
            message: '未查询到相关数据!'
          });
        }
      },function () {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      })
    }
  },
  created() {

  }

}
</script>
<style lang="scss" scoped>
@import '~@/assets/scss/subPage';
</style>
